 website with the same features, the cost can vary depending on a number of external factors, most importantly depending on the developer.")]),_vm._v(" "),_c('p',[_vm._v("That’s why this website calculator exists, it’s really hard (or even impossible) to put a proper price on a specific project, since every website is unique and has different requirements. It really comes down to the features you need for your website.")]),_vm._v(" "),_c('p',[_vm._v("However, we want to simplify this for you with this easy website calculator specifically made for estimating a given cost for a potential “WordPress” website.")]),_vm._v(" "),_c('p',[_vm._v("We therefore divided this in six chapters, being:")]),_vm._v(" "),_c('v-collapse-group',[_c('v-collapse-wrapper',{ref:"site-info"},[_c('h3',{directives:[{name:"collapse-toggle",rawName:"v-collapse-toggle"}],attrs:{"id":"site-info"}},[_vm._v("Site types")]),_vm._v(" "),_c('div',{directives:[{name:"collapse-content",rawName:"v-collapse-content"}]},[_c('p',[_vm._v("Here we have three categories for the site types:")]),_vm._v(" "),_c('h4',[_vm._v("Standard")]),_vm._v(" "),_c('p',[_vm._v("A standard website is what most people would need, this can range from a portfolio website to a small business one pager or landing page. These kinds of websites can easily range from €800 to €2000 excl. VAT.")]),_vm._v(" "),_c('p',[_vm._v("The price can vary depending on the various features you select, but as an starting point it is what most clients will need.")]),_vm._v(" "),_c('h4',[_vm._v("E-Commerce")]),_vm._v(" "),_c('p',[_vm._v("E-Commerce websites are a whole other story. A lot more thought needs to be added to creating an E-Commerce website, some features like Search Engine Optimisation (SEO), Social Media Optimisation (SMO) and Security are almost a definite must have, and can easily range from €3000 to €6000 excl. VAT.")]),_vm._v(" "),_c('h4',[_vm._v("Custom Production")]),_vm._v(" "),_c('p',[_vm._v("Custom Production websites are the prici